.main-allmembers-section {
  background-color: #150f22;
  padding-bottom: 120px;
}

.main-gradient {
  background-image: url("../../../../../assets/images/internalpages-bg.png");
  background-repeat: no-repeat;
  background-position: center;
  background-size: 100% 100%;
}

.heading-div {
  padding: 93px 0px;

  h1 {
    color: #fff;
    font-family: Urbanist;
    font-size: 48px;
    font-style: normal;
    font-weight: 600;
    line-height: normal;
  }

  p {
    color: #fff;
    font-family: Urbanist;
    font-size: 18px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
    max-width: 700px;
  }
}

.all-members-cards {
  margin-top: 70px;

  .container {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 20px;

    @media (max-width: 1200px) {
      grid-template-columns: repeat(3, 1fr);
    }

    @media (max-width: 992px) {
      grid-template-columns: repeat(2, 1fr);
    }

    @media (max-width: 576px) {
      grid-template-columns: 1fr;
      gap: 15px;
    }
  }
}

.membercards {
  border-left: 0.851px solid #ffffff31;
  padding: 0px 14px;
  text-align: left; // Default for desktop/tablet

  img {
    border-radius: 12px;
    width: 100%;
    height: auto;
  }

  h4 {
    color: rgba(255, 255, 255, 0.62);
    font-family: Urbanist;
    font-size: 14px;
    font-style: normal;
    font-weight: 400;
    line-height: 110.59%;
    text-transform: uppercase;
    margin-top: 18px;
  }

  h1 {
    color: #fff;
    font-family: Urbanist;
    font-size: 22px;
    font-style: normal;
    font-weight: 400;
    line-height: 119.097%;
  }

  p {
    color: #fff;
    font-family: Urbanist;
    font-size: 14px;
    font-style: normal;
    font-weight: 400;
    line-height: 138.056%;
    margin-top: 20px;
  }

  // Mobile view
  @media (max-width: 576px) {
    text-align: center;
  }
}
